:root {
  --text: rgb(0, 0, 0);
  --text-light: rgb(110, 110, 110);
  --background: rgb(255, 255, 255);
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", sans-serif;
  overflow: hidden;
  border: none;
  display: flex;
  justify-content: center;
}

.Heading {
  font-size: 1.75em;
  padding-bottom: 20px;
  height: 150px;
  background: rgb(129, 192, 255);
  padding: 20px;
  display: grid;
  place-items: center;
  color: rgba(255, 255, 255, 0.8);
}

li {
  padding-bottom: 12px;
}

.Screen {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  max-width: 1024px;
  justify-content: left;
}

.Container {
  justify-content: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* max-width: 414px; */
  padding-top: 5px;
  margin: 0 auto;
  height: 85vh;
  overflow-x: hidden;
  overflow-y: scroll;
  align-content: stretch;
  align-items: stretch;
}

.Container::-webkit-scrollbar {
  display: none;
}

.MyBalance {
  margin: 0px 10px;
}

.HeaderControls svg {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.Heading1 {
  width: 100vw;
  margin-top: 10px;
  padding: 15px 0px 0px 0px;
  font-size: 1em;
  font-weight: bold;
  text-align: center;
  color: var(--text-light);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  border-top: 1px solid hsl(0, 0%, 90%);
}

.Card {
  padding: 15px;
  /* color: #444; */
  position: relative;
  width: 100%;
  line-height: 1.35em;
}

/* #explainerCard {
  background-image: url("../src/images/journeyhero.png");
} */

.CardClose {
  width: 35px;
  height: 35px;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-light);
  cursor: pointer;
}

.CategoryCardNum {
  font-size: 30px;
}

.CategoryCardIcon {
  width: 85px;
  height: 85px;
  opacity: 1;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  margin-bottom: 7px;
  position: absolute;
  top: 30px;
  right: -25px;
}

.CategoryCardIcon svg {
  -webkit-transform: scale(3.5) rotate(0deg);
          transform: scale(3.5) rotate(0deg);
  opacity: 0.1;
  mix-blend-mode: overlay;
  color: black;
}

.CategoryCardIconOld svg {
  -webkit-transform: scale(2.5) translate(51%, 51%);
          transform: scale(2.5) translate(51%, 51%);
  opacity: 0.5;
  mix-blend-mode: screen;
}

.PageCardContainer {
  z-index: 2;
  width: 100.7vw;
  position: fixed;
  top: 120vh;
  min-height: 100vh;
  transition: 0.2s ease-in-out;
  max-width: 1000px;
}

.PageCardContainer ::-webkit-scrollbar {
  display: none;
}

.PageCard {
  background: rgb(249, 249, 255);
  border-radius: 20px 20px 0 0;
  border: 1px solid rgba(0, 0, 0, 0.05);
  /* box-shadow: 0px 0px 40px 1px rgba(0, 0, 0, 0.3); */
  color: rgb(40, 40, 40);
  height: 100vh;
  overflow: scroll;
  width: auto;
}

.PageCard h1 {
  font-size: 1.4em;
}

.PageCardContent {
  padding: 20px;
}

.PageClose {
  width: 35px;
  height: 35px;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
}

.PageCloseBg {
  width: 100%;
  height: 10vh;
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  top: -5vh;
  left: 0px;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  z-index: -1;
}

.PageIcon {
  color: white;
  mix-blend-mode: overlay;
  opacity: 0.2;
  position: absolute;
}

.PageIcon svg {
  -webkit-transform: scale(9) rotate(-10deg);
          transform: scale(9) rotate(-10deg);
}

.BottomNavigation {
  position: fixed;
  bottom: 0px;
  border-top: 1px solid #eee;
  height: 85px;
  background: white;
  width: inherit;
  max-width: inherit;
}

.WheelContainer {
  display: flex;
  place-items: center;
  justify-content: center;
}

.MuiFormControlLabel-root {
  padding-bottom: 10px;
}
