:root {
  --Highlight: rgb(238, 238, 238);
}
.post {
}
ol {
  padding-left: 0;
  border-radius: 16px;
}
.ul {
}
.posts a {
  color: #444;
}

.posts a:hover {
  color: #777;
}

.posts-list li {
  text-align: left;
}

.post h2 {
  margin-top: 1.25em;
  margin-bottom: 1.25em;
}

.post a,
#about a {
  border-bottom: 1px solid #8085c1;
}

.post a:hover {
  border: none;
}

/* standard */

.post p {
  font-size: 1em;
  margin-bottom: 1.45em;
  text-align: left;
  line-height: 1.5em;
}

.post ul,
.post ol {
  margin-left: 2.35em;
  margin-bottom: 1.25em;
  border-radius: 16px;
}

.post ol {
  margin-left: 0;
}

.post ul {
  background: var(--Highlight);
  list-style-position: inside;
  padding: 0.5em 0.5em 0.5em 1em;
  margin: 0;
}

.post ol li {
  font-weight: bold;
  list-style-position: inside;
  background: var(--Highlight);
  margin: 1em 0 0 0;
  line-height: 1.5em;
  font-size: 1.125rem;
  padding: 12px 16px;
  border-radius: 16px;
}

.post ol li ul {
  background: var(--Highlight);
  list-style-position: outside;
  margin: 0;
  padding-left: 0;
  color: #000;
}
.post ol li ul li {
  font-weight: normal;
  list-style-position: outside;
  background: var(--Highlight);
  padding: 0.25em;
  margin: 0 0 0em 1.25em;
  line-height: 1.5em;
  font-size: 0.95rem;
}

.post ul li {
  /*background: rgb(80, 144, 197);*/
  list-style-type: disc;
}
